<template>
  <div class="chat-detail-container">
    <TopBar>
      <template #center>
        <span>{{ title }}</span>
        <span class="participant-count">{{ participantCount }}</span>
      </template>
    </TopBar>

    <!--    -->
    <div class="user-info">
      <img
        :src="profileImageUrl"
        alt="Profile"
      />
      <span>{{ nickname }}</span>
    </div>

    <!--   -->
    <div
      v-if="notice"
      class="notice"
    >
      {{ notice }}
    </div>

    <!--    -->
    <div class="chat-content">
      <!--   -->
    </div>

    <!--   -->
    <div class="message-input">
      <input
        type="text"
        v-model="newMessage"
        placeholder=" ..."
        @keyup.enter="sendMessage"
      />
      <button @click="sendMessage"></button>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onBeforeUnmount } from 'vue';
import { TopBar } from '../myWalk/components';
import SockJS from 'sockjs-client';
import { Client } from '@stomp/stompjs';
import axios from 'axios'; // axios 
import { getCookie } from '@/utils';
import { useUserStore } from '@/stores/user/userState'; // Pinia Store 

// Pinia store   
const userStore = useUserStore();
const { email, name, nickname, profileImageUrl } = userStore;

// WebSocket    
const accessToken = getCookie('Authorization');

//   
const messages = ref([]); //   
const newMessage = ref(''); //   
const client = ref<Client | null>(null);
const connected = ref(false);

// 
const title = ref(' .');
const participantCount = ref(3);
const notice = ref(' :   .');
const chatId = ref('1');

//   
const subscribe = (destination: string) => {
  if (client.value) {
    client.value.subscribe(destination, (message) => {
      console.log('message:', message.body);
      const parsedMessage = JSON.parse(message.body);
      handleReceivedMessage(parsedMessage.currentChat); //     
    });
  } else {
    console.log('Client is not connected');
  }
};

//   
const handleReceivedMessage = (message: {
  userId: number;
  nickname: string;
  text: string;
  time: string;
  images?: { content: string; name: string }[];
}) => {
  messages.value.push(message);
  console.log('Processing message:', message);
};

// handlePlannerDetailResponse   
const handlePlannerDetailResponse = (message: string) => {
  console.log('Planner detail response:', message);
};

//    
const sendMessage = async () => {
  const data = {
    userId: userStore.id,
    nickname: userStore.nickname,
    numberTag: userStore.numberTag,
    profileImageUrl: userStore.profileImageUrl,
    message: newMessage.value,
    images: [], //   
  };

  try {
    await axios.post(
      `${process.env.VUE_APP_BASE_API}/sub/chatRooms/${chatId.value}/send`,
      {
        'current-chat': data,
      },
      {
        headers: {
          Authorization: `Bearer ${accessToken}`, //   
        },
      }
    );
    newMessage.value = '';
  } catch (error) {
    console.error('Failed to send message:', error);
  }
};

// WebSocket  (SockJS + STOMP )
const connect = async () => {
  try {
    const response = await axios.get(
      `${process.env.VUE_APP_BASE_API}/ws?token=${accessToken}`
    );

    if (response.status === 200) {
      client.value = new Client({
        webSocketFactory: () => {
          const socket = new SockJS(
            `${process.env.VUE_APP_BASE_API}/ws?token=${accessToken}`
          );

          socket.onclose = (event) => {
            console.log('socket closed', event);
          };
          return socket;
        },
        debug: (str) => {
          console.log(str);
        },
        reconnectDelay: 5000,
        heartbeatIncoming: 4000,
        heartbeatOutgoing: 4000,
      });

      //   
      client.value.onConnect = () => {
        connected.value = true;
        console.log('Connected successfully');
        subscribe(`/sub/chatRooms/${chatId.value}`);
      };

      client.value.onDisconnect = () => {
        connected.value = false;
        console.log('Socket disconnected');
      };

      client.value.onStompError = (frame) => {
        console.error(frame.headers['message']);
        console.error(frame.body);
      };

      client.value.activate();
    }
  } catch (e) {
    console.error(e);
  }
};

//    WebSocket  
onMounted(() => {
  connect();
});

//    WebSocket  
onBeforeUnmount(() => {
  if (client.value) {
    client.value.deactivate();
  }
});
</script>

<style scoped>
.chat-detail-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  box-sizing: border-box;
}

.notice {
  background-color: #f9f9f9;
  padding: 10px;
  text-align: center;
  font-weight: bold;
  border-bottom: 1px solid #ccc;
}

.chat-content {
  flex-grow: 1;
  overflow-y: auto;
  padding-top: 10px;
}

.message {
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.my-message {
  align-items: flex-end;
}

.message-sender {
  font-weight: bold;
}

.message-text {
  margin-top: 5px;
}

.message-time {
  color: #888;
  font-size: 12px;
}

.message-input {
  display: flex;
  gap: 10px;
  align-items: center;
}

.message-input input {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.message-input button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.message-images {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.message-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 8px;
}

.participant-count {
  color: #22f779;
  margin-left: 8px;
}
</style>
